<template>
  <div class="app">
    <div class="app-body">
      <slot name="sidebar"></slot>
      <main class="main">
        <AppHeader fixed>
          <b-link id="header-logo" @click="navigateHome" class="navbar-brand">
            <img
              src="@/assets/logo.png"
              class="navbar-brand-full navbar-brand-logo"
              alt="Expense Reduction Management Logo"
            />
          </b-link>
          <b-navbar-nav class="title-fw header-content-margin width-100">
            <div>
              <slot name="sidebarTogglerDefault"></slot>
            </div>
            <div
              id="thumbnailLogo"
              v-if="
                !isAdmin &&
                user_details.icon_path != '' &&
                user_details.icon_path != 'null' &&
                user_details.icon_path != 'default'
              "
            >
              <img
                v-bind:src="baseDOMAIN + user_details.icon_path"
                class="thumbnailLogo"
                alt="client_icon"
              />
            </div>
            <div id="thumbnailLogo" v-else>
              <img
                src="@/assets/DefaultIcon.png"
                class="thumbnailLogo"
                alt="client_icon"
              />
            </div>
            <div id="header-item-2" class="header-content-width client-fs ml-2">
              {{ user_details.clientCompanyName }}
            </div>
            <div id="profile-item" class="flag-button ml-2">
              <slot name="profileDropdown"></slot>
            </div>
            <b-link id="sp-logo" @click="navigateHome" class="navbar-brand">
              <img
                src="@/assets/logo.png"
                class="navbar-brand-full navbar-brand-logo"
                alt="Expense Reduction Management Logo"
              />
            </b-link>
            <div id="header-item-1" v-if="!isAdmin" class="flag-button w-100">
              <div class="float-right">
                <span class="point-label-fs"
                  >{{ $t("ËXCELLENTPOINT") }} /
                </span>
                <a href="javascript:;">
                  <span class="point-fs">{{ excelentPoint }}</span>
                </a>
              </div>
            </div>
          </b-navbar-nav>
          <slot name="sidebarToggler"></slot>
        </AppHeader>
        <slot name="breadcrumb"></slot>

        <div class="container-fluid">
          <div class="container-box">
            <router-view></router-view>
          </div>
        </div>
      </main>
    </div>
    <slot name="footer"></slot>
  </div>
</template>

<script>
import { Header as AppHeader } from "@coreui/vue";
import i18n from "@/plugins/i18n";
import { logValuesInSession } from "@/shared/utils";
import { Config } from "@/shared/config";
import { mapGetters } from "vuex";
export default {
  name: "RootContainer",
  components: {
    AppHeader,
  },
  data() {
    return {
      languages: [
        { caption: "English", state: false, language: "en", title: "ENGLISH" },
        { caption: "Japanese", state: false, language: "ja", title: "日本語" },
      ],
      baseDOMAIN: Config.apiDomain,
      basicInfo: {
        excellentPoint: localStorage.getItem("judg_company_point") + "P",
        label: localStorage.getItem("clientCompanyName"),
        name: localStorage.getItem("clientPersonName"),
      },
    };
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
      excelentPoint: "getExcellentPoint",
    }),
    btnStates() {
      return this.buttons.map((btn) => btn.state);
    },
    name() {
      return this.$route.name;
    },
    isAdmin() {
      return localStorage.getItem("role") === "ADMIN";
    },
  },
  created() {
    if (localStorage.getItem("locale")) {
      this.languages[
        this._.findIndex(this.languages, {
          language: localStorage.getItem("locale"),
        })
      ].state = true;
    } else {
      this.languages[1].state = true;
    }
  },
  methods: {
    changeLocale(locale) {
      this._.map(this.languages, (lang) => (lang.state = false));
      this.languages[
        this._.findIndex(this.languages, { language: locale })
      ].state = true;
      i18n.locale = locale;
      localStorage.setItem("locale", locale);
    },
    onFAQClick() {
      window.open(
        "https://www.jpx.co.jp/learning/tour/arrows/05.html",
        "_blank"
      );
    },
    navigateHome() {
      if (!this.isAdmin) {
        this.$router.push({ name: "user_dasboard" });
      }
      if (this.isAdmin) {
        this.$router.push({ name: "admin_dasboard" });
      }
      logValuesInSession("isAdmin", this.isAdmin);
    },
  },
};
</script>
<style scoped lang="scss">
$shadow-color: #dbd6d6;
.app-header {
  border: 0px;
  height: 80px;
}
.point-label-fs {
  font-size: 10px;
  color: #000000;
}
.point-fs {
  font-size: 16px;
  color: #000000;
}
.flag-button {
  margin-right: 5px;
}
.app-body {
  margin: 56px 10% 0% 10%;
  background: #ffffff;
}
.container-box {
  box-shadow: 0 0 10px $shadow-color;
  border-radius: 5px;
  padding: 1.2rem;
  margin-bottom: 1rem;
}

// toggle-btn
.btn-primary {
  color: #000000;
  background-color: #ffffff;
  border-color: #ac000c;
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: #ac000c;
  border-color: #ac000c;
}
.logo-p {
  padding: 2.5rem;
}
.main .container-fluid {
  padding: 0 5%;
  font-family: 游ゴシック !important;
}
@media (max-width: 991px) {
  .flag-button {
    margin-left: -7px;
  }
}
@media (max-width: 325px) {
  .header-content-margin {
    margin: -2% 15% 0% 5% !important;
  }
}
@media (max-width: 400px) {
  .header-content-margin {
    margin: -2% 13% 0% 5% !important;
  }
}
@media (max-width: 545px) {
  .header-content-margin {
    margin: -2% 11% 0% 5%;
  }
}
@media (max-width: 991px) {
  .header-content-margin {
    margin: -2% 4% 0% 5%;
  }
}
@media (max-width: 767px) {
  .header-content-margin {
    margin: -2% 11% 0% 5%;
  }
}
@media (min-width: 992px) {
  .header-content-margin {
    margin: -2% 5% 0% 5% !important;
  }
}
@media (max-width: 768px) {
  .sidebar-fixed.header-fixed.sidebar-lg-show.sidebar-show
    .header-content-margin {
    margin: -2% 35% 0% 5% !important;
  }
}
.header-content-width {
  min-width: fit-content;
}
.width-100 {
  width: 100%;
}
</style>
<style lang="scss">
.navbar-brand-logo {
  max-width: 100%;
  width: 100%;
}
.navbar-light .navbar-toggler {
  border: 0px solid #fff !important;
  float: right;
  padding: 0px;
}
#header-logo {
  display: none;
}
.client-fs {
  font-size: 12px;
  color: #000000;
}
.thumbnailLogo {
  height: 31px;
  width: 31px;
  min-width: 30px;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  // overflow: hidden;
  font-size: 0.555rem;
  border: 1px solid gray;
  padding: revert;
  cursor: default !important;
}

@media (min-width: 991px) {
  #sp-logo img {
    display: none;
  }
}
@media (max-width: 992px) {
  #header-item-1 {
    display: none;
  }
  #header-item-2 {
    display: none;
  }
  #thumbnailLogo img {
    display: none;
  }
  #thumbnailLogo i {
    display: none;
  }
  .width-100 {
    width: 10% !important;
  }
  #header-logo {
    display: block;
  }
}
@media (min-width: 992px) {
  .brand-minimized .app-header .navbar-brand .navbar-brand-full {
    display: block !important;
  }
}

@media (min-width: 992px) {
  .brand-minimized .app-header .navbar-brand {
    width: 205px !important;
  }
}
</style>
