export function getSideMenu(user) {
  if (user === "NOTADMIN") {
    return [
      {
        name: "BasicInformation",
        svgSrcImage: require("./assets/menu/setting.svg"),
        url: "/BasicInformation",
        icon: "fa fa-cogs",
      },
      {
        name: "CompanyInformation",
        svgSrcImage: require("./assets/menu/setting.svg"),
        url: "/CompanyInformation",
        icon: "fa fa-cogs",
      },
      {
        name: "OtherInformation",
        svgSrcImage: require("./assets/menu/setting.svg"),
        url: "/OtherInformation",
        icon: "fa fa-cogs",
      },
      {
        name: "ContractInformation",
        svgSrcImage: require("./assets/menu/setting.svg"),
        url: "/ContractInformation",
        icon: "fa fa-cogs",
      },
      {
        name: "CacheData",
        svgSrcImage: require("./assets/menu/setting.svg"),
        url: "/StoreData",
        icon: "fa fa-cogs",
      },
    ];
  } else {
    return [
      {
        name: "タスク処理",
        url: "/dashboard",
        icon: "fa fa-tasks",
        uID: "1",
      },
      {
        name: "顧客一覧",
        url: "/client/list",
        icon: "fa fa-list",
        uID: "2",
      },
      {
        name: "サプライヤー",
        url: "/supplier",
        icon: "fa fa-suitcase",
        uID: "3",
      },
      {
        name: "ユーザーマネージメント",
        url: "/usermanagement",
        icon: "fa fa-users",
        uID: "4",
      },
      //phase-2
      {
        name: "ポイントプログラム",
        url: "/point/program",
        icon: "fa fa-product-hunt",
        uID: "5",
        attributes: { disabled: true },
      },
      {
        name: "担当者管理",
        url: "/personIncharge",
        icon: "fa fa-user-secret",
        uID: "6",
      },
      //phase-2
      {
        name: "統計データルーム",
        url: "/statistical/data/room",
        icon: "fa fa-bar-chart",
        uID: "7",
        attributes: { disabled: true },
      },
      {
        name: "マスタメンテナンス",
        url: "/master/maintanence",
        icon: "fa fa-superpowers",
        uID: "8",
      },
    ];
  }
}
