<template>
  <span>
    <a
      :class="classList"
      v-on:click.stop.prevent
      v-if="isDisabled"
      v-bind="attributes"
      tabindex="-1"
    >
       <template v-if="svgSrcImage">
        <b-img
          :src="svgSrcImage"
          class="navbar-brand-full nav-icon"
          width="18%"
          alt=""
        />
      </template>
      <template v-else>
        <i :class="classIcon"></i>
      </template> <span class="nav-text">{{ name }}</span>
      <b-badge v-if="badge && badge.text" :variant="badge.variant">{{
        badge.text
      }}</b-badge>
    </a>
    <a
      :href="url"
      :class="classList"
      v-bind="attributes"
      v-else-if="isExternalLink"
    >
      <i :class="classIcon"></i> <span class="nav-text">{{ name }}</span>
      <b-badge v-if="badge && badge.text" :variant="badge.variant">{{
        badge.text
      }}</b-badge>
    </a>
    <router-link :to="url" :class="classList" v-bind="attributes" v-else>
      <template v-if="svgSrcImage">
        <b-img
          :src="svgSrcImage"
          class="navbar-brand-full nav-icon"
          width="18%"
          alt=""
        />
      </template>
      <template v-else>
        <i :class="classIcon"></i>
      </template>
      <span class="nav-text">{{ name }}</span>
      <b-badge v-if="badge && badge.text" :variant="badge.variant">{{
        badge.text
      }}</b-badge>
    </router-link>
  </span>
</template>

<script>
export default {
  name: "SidebarNavLink",
  props: {
    name: {
      type: String,
      default: "",
    },
    url: {
      type: String,
      default: "",
    },
    icon: {
      type: String,
      default: "",
    },
    svgSrcImage: {
      type: String,
      default: "",
    },
    badge: {
      type: Object,
      default: () => {},
    },
    variant: {
      type: String,
      default: "",
    },
    classes: {
      type: [String, Array, Object],
      default: "",
    },
    attributes: {
      type: Object,
      default: () => {
        return Object.create(null);
      },
    },
  },
  computed: {
    classList() {
      return [
        "nav-link",
        this.linkVariant,
        ...this.disabledClasses,
        ...this.attrClasses,
        ...this.itemClasses,
      ];
    },

    classIcon() {
      return ["nav-icon", this.icon];
    },
    linkVariant() {
      return this.variant ? `nav-link-${this.variant}` : "";
    },
    itemClasses() {
      return this.getClassArray(this.classes);
    },
    attrClasses() {
      return this.getClassArray(this.attributes.class);
    },
    disabledClasses() {
      return this.isDisabled ? "disabled".split(" ") : [];
    },
    isDisabled() {
      return Boolean(this.attributes.disabled);
    },
    isExternalLink() {
      return Boolean(this.url.substring(0, 4) === "http");
    },
  },
  methods: {
   getClassArray(classes) {
      let classArray = !classes
        ? []
        : typeof classes === "string" || classes instanceof String
        ? classes.split(" ")
        : Array.isArray(classes)
        ? classes
        : Object.keys(classes).filter((i) => classes[i]);
      if (!this.url) {
        classArray.push("remove-background");
      }
      return classArray;
    },
  },
};
</script>
<style scoped lang="scss">
.remove-background img{
  filter: invert(0%) !important;
  -webkit-filter: invert(0%) !important;
}
/**Enable while giving mng Build */
// .sidebar .nav-link.active:hover .nav-icon {
//   filter: invert(100%) !important;
//   -webkit-filter: invert(100%) !important;
// }
// .sidebar-minimized .sidebar .nav-link.active:hover .nav-icon{
//   filter: invert(0%) !important;
//   -webkit-filter: invert(0%) !important;
// }
 .sidebar .nav-item:hover .nav-link{
  background: #ededed !important;
  color: #000 !important;
}
.sidebar-minimized .sidebar .nav-item:hover .nav-link{
  background: #ededed !important;
  color: #000 !important;
}
.sidebar .nav-link.disabled img{
    color: #b3b3b3 !important;
    cursor: default;
    background: transparent;
    filter: invert(75%) !important;
  -webkit-filter: invert(75%) !important;
}
</style>