<template>
  <div class>
    <RootContainer>
      <template slot="sidebarToggler">
        <SidebarToggler
          :mobile="true"
          class="d-lg-none d-sm-show d-md-show"
          display="md"
        >
        </SidebarToggler>
        <span
          v-if="!isAdmin && unread_messages > 0"
          class="dot side-mobile-badge d-lg-none d-sm-show d-md-show"
        ></span>
      </template>

      <template slot="sidebarTogglerDefault" v-if="!showSideMenu">
        <div>
          <SidebarToggler
            :defaultOpen="showSideMenu"
            @sidebar-toggle="sidebarToggle"
            class="d-md-down-none rel"
            display="lg"
          />
        </div>

        <span
          v-if="!isAdmin && unread_messages > 0"
          class="badge badge-danger badge-pill"
          >{{ unread_messages }}</span
        >
      </template>
      <template slot="profileDropdown">
        <ProfileDropdownList />
      </template>

      <template slot="sidebar">
        <AppSidebar fixed>
          <div class="navbar-brand">
            <span v-if="showSideMenu"
              ><SidebarToggler
                :defaultOpen="showSideMenu"
                @sidebar-toggle="sidebarToggle"
                class="d-md-down-none rel"
                display="lg"
              />
              <span
                v-if="!isAdmin && unread_messages > 0"
                class="badge side-badge badge-danger badge-pill"
                >{{ unread_messages }}</span
              ></span
            >
            <img
              id="logo-app"
              @click="navigateHome"
              src="@/assets/logo.png"
              width="60%"
              class="navbar-brand-full logo-p navbar-brand-logo pointer"
              alt="Expense Reduction Management Logo"
            />
          </div>

          <slot name="sidebarTogglerDefault"></slot>
          <SidebarHeader
            ><div v-if="isAdmin" class="title-fs">
              {{ $t("ADMINSIDEBARHEADER") }}
            </div>
          </SidebarHeader>
          <SidebarForm />
          <SidebarNav :navItems="getNavItems"></SidebarNav>
          <SidebarFooter />
          <SidebarMinimizer />
        </AppSidebar>
      </template>
      <template slot="breadcrumb">
        <Breadcrumb />
      </template>
      <template slot="footer">
        <Footer>
          <b-row id="web-item" class="footer-text footer-left">
            <b-col cols="7">
              <div class="float-left">
                <a
                  v-if="!isAdmin"
                  @click="openTerms"
                  href="javascript:;"
                  class="footer-link-padding footer-font"
                >
                  {{ $t("TERMSOFSERVICE") }}
                </a>
                <a href="javascript:;" class="footer-link-padding footer-font">
                  {{ $t("PRIVACYPOLICY") }}
                </a>
                <a
                  @click="openQAndA"
                  href="javascript:;"
                  class="footer-link-padding footer-font"
                >
                  {{ $t("QANDA") }}
                </a>
                <a
                  @click="openInquary"
                  href="javascript:;"
                  class="footer-link-padding footer-font"
                >
                  {{ $t("ENQUIRY") }}
                </a>
              </div>
            </b-col>
            <b-col cols="5">
              <div class="float-right padding-0">
                <span>{{ $t("COPYRIGHT") }}</span>
              </div>
            </b-col>
          </b-row>
          <b-row id="mobile-item" class="footer-text" >
            <b-col cols="12" class="text-center mb-2 mt-2">
              <a
                v-if="!isAdmin"
                @click="openTerms"
                href="javascript:;"
                class="footer-link-padding footer-font"
              >
                {{ $t("TERMSOFSERVICE") }}
              </a>
            </b-col>
            <b-col cols="12" class="text-center mb-2 mt-2">
              <a href="javascript:;" class="footer-link-padding footer-font">
                {{ $t("PRIVACYPOLICY") }}
              </a>
            </b-col>
            <b-col cols="12" class="text-center mb-2 mt-2">
              <a
                @click="openQAndA"
                href="javascript:;"
                class="footer-link-padding footer-font"
              >
                {{ $t("QANDA") }}
              </a>
            </b-col>
            <b-col cols="12" class="text-center mb-2 mt-2">
              <a
                @click="openInquary"
                href="javascript:;"
                class="footer-link-padding footer-font"
              >
                {{ $t("ENQUIRY") }}
              </a>
            </b-col>
            <b-col class="mb-3 mt-3">
              <div class="text-center padding-0">
                <span>{{ $t("COPYRIGHT") }}</span>
              </div>
            </b-col>
          </b-row>
        </Footer>
      </template>
    </RootContainer>
  </div>
</template>

<script>
import { getSideMenu } from "@/_nav";
import _ from "lodash";
ProfileDropdownList;
import RootContainer from "./RootContainer";
import Breadcrumb from "@/components/Breadcrumb";
import { readACL, checkChildrenACL } from "@/shared/utils";
import ProfileDropdownList from "@/components/ProfileDropdownList";
import { mapGetters } from "vuex";
import { logValuesInSession } from "@/shared/utils";
import SidebarNav from "@/components/sidebar/SidebarNav";
import SidebarToggler from "@/components/sidebar/SidebarToggler";
import api from "@/shared/api";
import { endPoint } from "@/shared/endPoint";
import {
  Sidebar as AppSidebar,
  SidebarFooter,
  SidebarForm,
  SidebarHeader,
  SidebarMinimizer,
  Footer,
} from "@coreui/vue";

export default {
  name: "RootSidebarContainer",
  components: {
    RootContainer,
    AppSidebar,
    SidebarFooter,
    SidebarForm,
    SidebarHeader,
    SidebarMinimizer,
    SidebarNav,
    SidebarToggler,
    Breadcrumb,
    ProfileDropdownList,
    Footer,
  },
  data() {
    return {
      navItems: [],
      timer: "",
      notify: "",
      lang: "",
      admin_flg: false,
      supplier_flag: false,
      wiz_flag: false,
      showSideMenu: true,
    };
  },
  computed: {
    name() {
      return this.$route.name;
    },
    isAdmin() {
      return localStorage.getItem("role") === "ADMIN";
    },
    getNavItems() {
      return this.navItems;
    },
    ...mapGetters({
      unread_messages: "getUnreadMessages",
    }),
  },
  beforeDestroy() {
    clearInterval(this.timer);
    clearInterval(this.notify);
  },
  created() {
    this.admin_flg = JSON.parse(localStorage.getItem("admin_auth"));
    this.supplier_flag = JSON.parse(localStorage.getItem("supplier_auth"));
    this.wiz_flag = JSON.parse(localStorage.getItem("wiz_auth"));

    this.lang = localStorage.getItem("locale");
    let menus = [];
    if (localStorage.getItem("role") === "ADMIN") {
      menus = getSideMenu("ADMIN");
    } else {
      menus = getSideMenu("NOTADMIN");
    }
    this.aclCheck(menus);
    this.timer = setInterval(this.checkLanguageChange, 100);
    if (localStorage.getItem("role") != "ADMIN") {
      this.notify = setInterval(this.getUnreadNotifications, 300000);
    }
  },
  methods: {
    sidebarToggle() {
      this.showSideMenu = !this.showSideMenu;
    },
    navigateHome() {
      if (!this.isAdmin) {
        this.$router.push({ name: "user_dasboard" });
      }
      logValuesInSession("isAdmin", this.isAdmin);
    },
    async getUnreadNotifications() {
      const { data } = await api.post(endPoint.MESSAGEREDORUNREDCOUNT, {
        client_no: localStorage.getItem("client_account_no"),
      });
      if (data.success) {
        this.$store.dispatch("updateUnreadMessages", {
          unread_messages: data.data.messageCount,
        });
      }
    },
    aclCheck(menus) {
      let authorizedMenu = [];

      if (localStorage.getItem("role") === "ADMIN") {
        menus.forEach((menu) => {
          if (menu.uID) {
            /** Check user has access to  */
            const rule = this.readMenuACL(menu.uID);
            if (rule) {
              authorizedMenu.push(menu);
            }
          }
        });
      } else {
        console.log("menu", menus);

        authorizedMenu = menus;
      }

      this.navItems = authorizedMenu;
    },
    readMenuACL(id) {
      let adminAcl = this.admin_flg;
      let wizAcl = this.wiz_flag && !(id == "5" || id == "8");
      let supplierAcl =
        this.supplier_flag && !(id == "4" || id == "5" || id == "7");
      let conditionAcl = adminAcl || wizAcl || supplierAcl;
      return conditionAcl;
    },
    aclCheck1(menus) {
      const authorizedMenu = [];
      _.forEach(menus, function (menu) {
        if (menu.uID) {
          /** Check user has access to  */
          const rule = readACL(menu.uID);
          if (rule && rule.canAccess === 1) {
            if (menu.children) {
              const menuACLFiltered = menu;
              const children = checkChildrenACL(menu.children);
              /** submenu children */
              _.forEach(menuACLFiltered.children, function (submenu) {
                if (submenu.children) {
                  submenu.children = checkChildrenACL(submenu.children);
                }
              });
              menuACLFiltered.children = children;
              authorizedMenu.push(menuACLFiltered);
            } else {
              authorizedMenu.push(menu);
            }
          }
        } else {
          if (menu.children) {
            const menuACLFiltered = menu;
            const children = checkChildrenACL(menu.children);
            /** submenu children */
            _.forEach(menuACLFiltered.children, function (submenu) {
              if (submenu.children) {
                submenu.children = checkChildrenACL(submenu.children);
              }
            });
            menuACLFiltered.children = children;
            authorizedMenu.push(menuACLFiltered);
          } else {
            authorizedMenu.push(menu);
          }
        }
      });
      this.navItems = authorizedMenu;
    },
    checkLanguageChange() {
      if (localStorage.getItem("locale") !== this.lang) {
        this.lang = localStorage.getItem("locale");
        let menus = [];
        if (localStorage.getItem("role") === "ADMIN") {
          menus = getSideMenu("ADMIN");
        } else {
          menus = getSideMenu("NOTADMIN");
        }
        this.aclCheck(menus);
      }
    },
    cancelCheckLanguageChange() {
      clearInterval(this.timer);
    },
    openTerms() {
      let route = this.$router.resolve({ path: "/termsandconditions" });
      window.open(route.href, "_blank");
    },
    openQAndA() {
      let route = this.$router.resolve({ path: "/qna" });
      window.open(route.href, "_blank");
    },
    openInquary() {
      let route = this.$router.resolve({ path: "/contact" });
      window.open(route.href, "_blank");
    },
  },
};
</script>
<style lang="scss" scoped>
