<template>
  <AppHeaderDropdown right no-caret>
    <template slot="header">
      <a href="javascript:;">
        {{ user_details.clientPersonName }}
      </a>
    </template>
    <template slot="dropdown">
      <b-dropdown-item @click="logout">
        <i class="fa fa-lock" />
        {{ $t("LOGOUT") }}
      </b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>

<script>
import { HeaderDropdown as AppHeaderDropdown } from "@coreui/vue";
import { mapGetters } from "vuex";
export default {
  name: "ProfileDropdownList",
  components: {
    AppHeaderDropdown,
  },
  data: () => {
    return {};
  },
  computed: {
    ...mapGetters({
      user_details: "getUserDetails",
    }),
    clientPersonName() {
      return localStorage.getItem("clientPersonName");
    },
    username() {
      return localStorage.getItem("clientName");
    },
  },
  methods: {
    /**Funtion to destroy the login session and clear session data */
    async logout() {
      let checkedExpenseItemCodeArray = [];
      this.$store.dispatch(
        "updateCheckedExpItemCodeArrayInDasboard",
        checkedExpenseItemCodeArray
      );
      if (localStorage.getItem("role") == "ADMIN") {
        localStorage.clear();
        this.$router.push({ name: "admin_login" });
      } else {
        localStorage.clear();
        this.$router.push("/");
      }
    },
  },
};
</script>
<style lang="scss" >
.b-nav-dropdown .dropdown-menu.dropdown-menu-right.show {
  top: -32px !important;
  left: 10.5rem !important;
}
@media (max-width: 992px) {
  .b-nav-dropdown .dropdown-menu.dropdown-menu-right.show {
    left: 7rem !important;
    top: -29px !important;
  }
}
</style>
