<template>
  <div>
    <nav aria-label="breadcrumb">
      <ol class="breadcrumb">
        <li v-for="(breadcrumb, index) in breadcrumbList" :key="index" class="breadcrumb-item">
          <a v-if="index + 1 != breadcrumbList.length" @click="routeTo(index)" href="javascript:;">
            {{
            $t(breadcrumb.name)
            }}
          </a>
          <span v-if="index + 1 == breadcrumbList.length" class="font-weight-bold">
            {{
            $t(breadcrumb.name)
            }}
          </span>
        </li>
      </ol>
    </nav>
  </div>
</template>

<script>
/**
 * @description component to show bread crum above the shown page
 * @name Breadcrumb
 */
export default {
  name: "Breadcrumb",

  data() {
    return {
      breadcrumbList: []
    };
  },
  computed: {},
  watch: {
    $route() {
      this.updateList();
    }
  },
  mounted() {
    this.updateList();
  },
  methods: {
    routeTo(pRouteTo) {
      if (this.breadcrumbList[pRouteTo].link)
        this.$router.push(this.breadcrumbList[pRouteTo].link);
    },
    updateList() {
      this.breadcrumbList = this.$route.meta.breadcrumb;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
